export const SwiperComponent = () => {
  return (
    <div className="swiper-wrapper--custom">
      <h1 className="Swiper-header">
          <span>253</span>
      </h1>
      <p className="Swiper-header__text">
        <span>4,5</span>{' '}
        {[
          <StarIcon gold />,
          <StarIcon gold />,
          <StarIcon gold />,
          <StarIcon gold />,
          <StarIcon gold={false} />,
        ]}
      </p>
      <Swiper
        slidesPerView={3}
        spaceBetween={20}
        breakpoints={{
          0: {
            slidesPerView: 1,
            spaceBetween: 10,
          },
          800: {
            slidesPerView: 2,
            spaceBetween: 15,
          },
          1300: {
            slidesPerView: 3,
            spaceBetween: 20,
          },
        }}
        autoHeight={true}
        navigation={true}
        modules={[Navigation]}>
        {reviewsList.map((value, index) => {
          if (index % 2 !== 0) return null;

          return (
            <SwiperSlide>
              <SwiperItem key={index} {...value} />
              {reviewsList[index + 1] && <SwiperItem {...reviewsList[index + 1]} key={index + 1} />}
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
  );
};
