import styles from './AboutUs.module.scss';
import image from '../../assets/AboutUsBack.png';
import { MainIcon } from '../../icons/MainIcon/MainIcon';
import {
  AboutUsClock,
  AboutUsClockPeople,
  AboutUsReward,
  AboutUsDollar,
  AboutUsLike,
  AboutUsHands,
  AboutUsPeoples,
} from '../../icons/AboutUs/AboutUsIcons';
import { RedButton } from '../RedButton/RedButton';

const iconsArr = [
  { icon: <AboutUsClock />, text: '   ' },
  { icon: <AboutUsClockPeople />, text: ' .    24/7' },
  { icon: <AboutUsReward />, text: '   5  ' },
  { icon: <AboutUsDollar />, text: '     30%   ' },
  { icon: <AboutUsLike />, text: '   ' },
  { icon: <AboutUsHands />, text: ' ' },
  { icon: <AboutUsPeoples />, text: '  ' },
];

export const AboutUs = () => {
  return (
    <div className={styles.wrapper}>
      <section className={styles.container}>
        <div className={styles.content}>
          <div className={styles.info}>
            <h1> </h1>
            <p>
               AUTOCENTER       
              .        .  
                   ,    
              .
            </p>
          </div>

          <img src={image} alt="cars" />
          <MainIcon />
        </div>

        <div className={styles.icons__container}>
          {iconsArr.map((iconData, index) => {
            return (
              <div className={styles.icons} key={index}>
                {iconData.icon} <span>{iconData.text}</span>
              </div>
            );
          })}
        </div>

        <RedButton text=" " />
      </section>
    </div>
  );
};
